import { HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpService } from '@ecp-caf/caf-common';
import { Observable } from 'rxjs';
import { FileDeleteEntity, FileUploadEntity } from '../entities/attachment';

@Injectable()
export class TaskCommentService {

  constructor(
    private httpSvc: HttpService,
  ) { }

  static newId() {
    const S4 = () => (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1);
    return (S4() + S4() + '-' + S4() + '-' + S4() + '-' + S4() + '-' + S4() + S4() + S4());
  }


  getCommentList(): Observable<any> {
    const url = `/api/runtime/wf/v1.0/commonComments`;
    return this.httpSvc.get(url);
  }

  addComment(): Observable<any> {
    const url = `/api/runtime/wf/v1.0/commonComments`;
    return this.httpSvc.post(url, {});
  }

  /**
    * è·åä»»å¡ä¸­å¿éç½®é¡¹
    * @param code éç½®é¡¹çCode
    */
  getTaskCenterConfigByCode(code: string): Observable<any> {
    const url = `/api/runtime/task/v1.0/task-center-setting/code?code=${code}`;
    return this.httpSvc.get(url);
  }

  /**
   * çæ­£ä¸ä¼ éä»¶
   * @param files
   */
  uploadAttachment(files: FileUploadEntity): Observable<any> {
    let url = `/api/runtime/dfs/v1.0/doc/commit`;
    return this.httpSvc.put(url, files);
  }

  /**
   * å é¤éä»¶
   * @param fileDeleteEntity 
   */
  deleteAttachment(fileDeleteEntity: FileDeleteEntity): Observable<any> {
    let url = `/api/runtime/dfs/v1.0/doc`;
    return this.httpSvc.delete(url, fileDeleteEntity);
  }
}
