import {
  Component,
  OnInit,
  Input,
  ViewChild,
  forwardRef,
  Injector,
  ComponentFactoryResolver,
  Output, EventEmitter, Optional
} from '@angular/core';
import { TaskCommentService } from '../services/task-comment.service';
import { TaskCommentViewModel } from './task-comment.viewmodel';
import { TaskCommentUIState } from './task-comment.uistate';
import { DialogComponent } from '@farris/ui-dialog';
import { ControlValueAccessor, NG_VALUE_ACCESSOR, FormGroup, FormBuilder } from '@angular/forms';
import { CommonComment } from '../entities/comment';
import { GridComponent } from '@progress/kendo-angular-grid';
import { FDropdownDirective } from '@farris/ui-dropdown';
import { UploadDialogService } from '@gsp-svc/formdoc-upload';
import { DatagridComponent } from "@farris/ui-datagrid";
import { MessagerService } from '@farris/ui-messager';
import { FileDeleteEntity, FileUploadEntity } from '../entities/attachment';

@Component({
  selector: 'wf-comment',
  templateUrl: './task-comment.component.html',
  styleUrls: ['./task-comment.component.scss'],
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => TaskCommentComponent),
    multi: true
  },]
})

/**
 * æ­¤å·¥ç¨å·²åºå¼ä¸åä½¿ç¨ï¼æ´æ°ä¸ºwf-task-handlerçtask-commentç»ä»¶ by fangpengfei@2021.1.13
 */
export class TaskCommentComponent implements OnInit, ControlValueAccessor {

  // é»è®¤åçæè§
  @Input() comment: string;
  @Input() placeholder = '';
  @Input() processInstanceId: string;
  @Input() set actionCode(actionCode: string) {
    if (actionCode === 'Claim' || actionCode === 'AssignUser') {
      this.showUploadAttachment = false;
    } else {
      this.showUploadAttachment = true;
    }
  }

  @Output() attachment: any = new EventEmitter();
  @ViewChild(DialogComponent) commentDialog: DialogComponent;
  @ViewChild('commentGrid') commentGrid: GridComponent;
  @ViewChild('dropdownEl') dropDownEl: FDropdownDirective;//è§£å³éæ©æè§åï¼ä¸æèåæ æ³æ¶æçé®é¢
  @ViewChild('grid') grid: DatagridComponent;
  
  private _value = '';
  attachmentList: any[] = [];
  selectValue: string;
  textAreaHeight: number = 120;
  messageService: MessagerService;
  isRepeat = false; // éå¤éä»¶æ ¡éª
  ifShowAttachment: boolean = true;//ä»»å¡ä¸­å¿éç½®ï¼æ¯å¦æ¾ç¤ºéä»¶ä¸ä¼ 
  showUploadAttachment = true;

  set value(v) {
    if (this._value !== v) {
      this._value = v;
      this.onModelChange(v);
    }

  }
  get value() {
    return this._value;
  }

  @Input()
  set TextAreaHeight(height: number) {
    this.textAreaHeight = height;
  }

  onModelChange = (obj: any) => { };
  onModelTouched = (obj: any) => { };

  uploadDialog: UploadDialogService;
  resolver: ComponentFactoryResolver;
  // æé æ¹æ³ä¸å¯å¨ï¼é¡ºåºä¸è¦æ¹åï¼ä½¿ç¨æ°ç
  constructor(
    private commentSvc: TaskCommentService,
    public viewModel: TaskCommentViewModel,
    public uiState: TaskCommentUIState,
    private formBuilder: FormBuilder,
    @Optional() private injector: Injector,
  ) {
    this.injector && (this.uploadDialog = this.injector.get(UploadDialogService));
    this.injector && (this.messageService = this.injector.get(MessagerService));
  }

  ngOnInit() {
    // è·åå¸¸ç¨æè§åè¡¨
    this.loadComments();
    this.commentSvc.getTaskCenterConfigByCode('If_Show_Attachment').subscribe(re => {
      re && re.currentValue === 'false' && (this.ifShowAttachment = false);
    })
  }

  onEditComment() {
    this.commentDialog.show();
  }

  save() {
    this.commentGrid.closeCell();
    this.viewModel.save().subscribe(r => {
      this.loadComments();
      this.commentDialog.close();
    });
  }

  loadComments() {
    this.commentSvc.getCommentList().subscribe((data: any[]) => {
      this.uiState.commentList = data;
      this.uiState.originCommentIds = data.map(d => d.id);
      this.uiState.comments = data.map(d => d.message);
    });
  }

  public cellClickHandler({ sender, rowIndex, columnIndex, dataItem, isEdited }) {
    this.uiState.curComment = dataItem;
    if (!isEdited) {
      sender.editCell(rowIndex, columnIndex, this.createFormGroup(dataItem));
    }
  }

  public cellCloseHandler(args: any) {
    const { formGroup, dataItem, column } = args;
    if (!formGroup.valid) {
      args.preventDefault();
    } else if (formGroup.dirty) {
      if (this.uiState.originCommentIds.findIndex(d => d === dataItem.id) > -1
        && this.uiState.changesCommentIds.findIndex(id => id === dataItem.id) < 0) {
        this.uiState.changesCommentIds.push(dataItem.id);
      }
      Object.assign(dataItem, formGroup.value);
    }
  }

  public createFormGroup(dataItem: CommonComment): FormGroup {
    return this.formBuilder.group({
      message: dataItem.message,
      sortOrder: dataItem.sortOrder,
    });
  }

  writeValue(value: string) {
    if (value !== this.value) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.onModelChange = fn;
  }

  registerOnTouched(fn) {
    this.onModelTouched = fn;
  }

  buttonClick(data: any) {
    this.dropDownEl.close();
    this.value = data;
  }

  uploadAttachment() {
    if (this.uploadDialog) {
      this.uploadDialog.uploadFile(this.processInstanceId, 'wf_attachment').then(re => {
        for (let i of (re as any[])) {
          this.isRepeat = false;
          for (let attach of this.attachmentList) {
            if (attach.fileName === i.fileName) {
              this.messageService.info('åå«éå¤æä»¶' + i.fileName);
              this.isRepeat = true;
              break;
            }
          }
          if (!this.isRepeat) {
            this.attachmentList.push(i);
          }
        }
        this.attachment.emit(this.attachmentList);
        //æ§è¡ä¸ä¼ éä»¶
        let files = new FileUploadEntity();
        files.rootId = "wf_attachment";
        files.metadataIdList = [];
        this.attachmentList.forEach( a => {
          files.metadataIdList.push(a.metadataId);
        })
        files.metadataIdList.length && this.commentSvc.uploadAttachment(files).subscribe(() => {});
      });
    } else {
      throw new Error("ä½¿ç¨éä»¶ä¸ä¼ åè½ï¼è¯·åçº§è¡¨å");
    }
  }

  attachmentDelete(attachment) {
    var gspDocDeleteEntity: FileDeleteEntity = {
      rootId: "wf_attachment",
      metadataId: attachment.metadataId,
      mode: "Formal"
    }
    let index = this.attachmentList.indexOf(attachment);
    if (index > -1) {
      this.attachmentList.splice(index, 1);
    }
    this.attachment.emit(this.attachmentList);
    this.commentSvc.deleteAttachment(gspDocDeleteEntity).subscribe(() => {});
  }

  imageUrl(attachmentInfo) {
    return '/platform/runtime/wf/webapp/mobiletaskcenter/assets/accessory/' + this.createItem(attachmentInfo) + '.svg';
  }

  //è§£ææä»¶ç±»åï¼ç»åºç¼©ç¥å¾
  createItem(attachmentInfo) {
    var pos = attachmentInfo.fileName.lastIndexOf(".");
    var type = attachmentInfo.fileName.substring(pos + 1);
    type = (type == '' ? 'Default' : type.toLowerCase());
    var docLib = 'doc-docx-htm-html-js-pdf-ppt-pptx-rar-zip-txt-xls-xlsx';
    var videoLib = 'wav-aif-aiff-au-mp1-mp2-mp3-ra-rm-ram-mid-rmi';
    var audioLib = 'avi-rm-rmvb-mpeg-mpg-dat-mov-qt-asf-wm-mp4';
    var imgLib = 'webp-baibmp-dupcx-tif-gif-jpeg-tga-exif-fpx-svg-psd-cdr-pcd-dxf-ufo-eps-ai-png-hdri-raw-wmf-flic-emf-ico-jpg';
    if (docLib.indexOf(type) >= 0) {
      type = type.toLowerCase();
    } else if (videoLib.indexOf(type) >= 0) {
      type = 'video';
    } else if (audioLib.indexOf(type) >= 0) {
      type = 'audio';
    } else if (imgLib.indexOf(type) >= 0) {
      type = 'picture';
    } else {
      type = 'default';
    }
    return type;
  }
}
